import React, { useState, useEffect } from "react";
import { withCookies } from "react-cookie";
import axios from "axios";
import { Card, Container, Col, Row } from "react-bootstrap";

const ApiFetch = (props) => {
  const token = props.cookies.get("current-token");
  // console.log(token)
  const [news, setNews] = useState([]);

  useEffect(() => {
    axios
      .get("http://54.250.166.243/api/v1/news/", {
        headers: {
          Authorization: `Token ${token}`,
        },
      })
      .then((res) => {
        setNews(res.data);
      });
  }, [token]);

  return (
    <div>
      {news.map((news) => (
        <Container>
          <ul>
            <li key={news.id}>
              <Card className="card">
                <Card.Body>
                  <Row>
                    <Col sm={3}>
                      <a href={news.link}>
                        <img src={news.image} alt="news_img" />
                      </a>
                    </Col>
                    <Col sm={9}>
                      <Card.Title>{news.title} </Card.Title>
                      <Card.Text className="card-body">
                        {news.body.slice(0, 105)}...
                      </Card.Text>
                      <a href={news.link} class="btn btn-outline-secondary">
                        
                      </a>
                      <Card.Text>{news.published}</Card.Text>
                    </Col>
                  </Row>
                </Card.Body>
              </Card>
            </li>
          </ul>
          {/* <ReactPaginate
            pageCount={Math.ceil(news.id.length / 4)} // / 1
            marginPagesDisplayed={2} //21,22, 1 
            pageRangeDisplayed={5} //
            onPageChange={this.pageChange} //()
            containerClassName='pagination' //
            pageClassName='page-item' //(li)
            pageLinkClassName='page-link' //
            activeClassName='active' // 
            previousLabel='<' //
            nextLabel='>' //
            previousClassName='page-item' // '<'(li)
            nextClassName='page-item' //'>'(li)
            previousLinkClassName='page-link'  //'<'
            nextLinkClassName='page-link'//'>'
            disabledClassName='disabled' // or ()
            breakLabel='...' // 
            breakClassName='page-item' // 
            breakLinkClassName='page-link' // 
          /> */}
        </Container>
      ))}
    </div>
  );
};

export default withCookies(ApiFetch);
